*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
/* ---------------------------------------------------------------------------------------------------------- */
nav{
    width: 100%;
    height: auto;
    background-color: cadetblue;
    color: #040714; 
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    justify-content: space-around;
    label{
        padding: 10px;
        text-transform: uppercase;
    }
}
.popup{
    height: auto;
    width: 80%;
    margin: auto;
    background-color: #040714;
    position: fixed;
    color: aliceblue;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    filter: drop-shadow(15px 15px 120px #040714);
    .ul_popup{
        list-style: none;
        padding: 20px;
        .li_popup{
            padding: 9px;
        }
    }
}



