#list-episodes{
    display: flex;
    flex-wrap: wrap;
    /* padding: 10px 30px; */
    padding: 10px 0px 10px 30px;

}
/* .container,  */
.personnage-name{
    height: 150px;
    width: 22%;
    background-color: cadetblue;
    margin: 0 30px 10px 0;
    display: block;
    margin-top: 80px;
}

.species_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-evenly;
}
.specie{
    height: 380px;
    width: 300px;
    background-color: cadetblue;
    margin: 0 30px 10px 0;
    display: block;
    margin-top: 80px;
}
.image{
    height: 80px;
    width: 80px;
    position: relative;
    margin-top: -5%;
    margin-left: 35%;
}

.img{
    border-radius: 50%;
    width: 80px;
    height: 80px;
    margin: auto;
}

.img:hover{
    width: 100px;
    height: 100px;
    transform: .2s ease-in-out;
    filter: grayscale(100%);
    transform : scale(1, 1);
}



